* {
   margin: 0;
   padding: 0;
}

.app{
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.board{
    width: calc(64px * 8);
    height: calc(64px * 8);
    display: flex;
    flex-wrap: wrap;
}

.cell{
    width: 64px;
    height: 64px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.cell img{
    height: 50px;
    width: 35px;
    position: relative;
}

 .black{
    background-color: rgba(249,208,214,255);
 }

 .white{
    background-color: rgba(250,240,248,255);
 }

 .selected {
    background-color: rgb(169, 59, 59);
 }

 .available {
    height: 12px;
    width: 12px;
    border-radius: 50%;
    background-color: #22c022;
 }